import * as THREE from 'three';
import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { RoundedBoxGeometry } from 'three/examples/jsm/geometries/RoundedBoxGeometry';

import classNames from './styles.module.css';
import { isWithinViewport } from '../../../../utils';

const BLUE = 0x0d58d3;
const CUBE_SIZE = 0.13;
const GREY = 0xffffff;
const MAX_CUBES = 50;

function generateMeshes(): THREE.Mesh[] {
  const material = (count: number) =>
    new THREE.MeshStandardMaterial({
      metalness: 0.25,
      flatShading: false,
      roughness: 0.3,
      color: new THREE.Color(count % 3 === 0 ? GREY : BLUE),
    });
  const meshes: THREE.Mesh[] = [];

  for (let count = 0; count < MAX_CUBES; count += 1) {
    const radius = CUBE_SIZE * 0.1;
    const geometry = new RoundedBoxGeometry(CUBE_SIZE, CUBE_SIZE, CUBE_SIZE, 2, radius);
    const mesh = new THREE.Mesh(geometry, material(count));
    // Spread positions more widely to avoid overlaps during helix motion
    const t = count / MAX_CUBES;
    mesh.position.set(
      Math.random() * 0.4 - 0.2,
      Math.random() * 5,
      Math.random() * 0.6 - 0.3
    );
    meshes.push(mesh);
  }

  return meshes;
}

export interface DeepMindCubesProps {
  slideContainer: HTMLDivElement | null;
}

export default function DeepMindCubes(props: DeepMindCubesProps) {
  const { slideContainer } = props;
  const canvasContainerRef = useRef<HTMLDivElement>(null);
  const rendererRef = useRef<THREE.WebGLRenderer | undefined>(undefined);
  const rotationRef = useRef<number>(0);
  const lastScrollRef = useRef<number>(0);
  const [isVisible, setIsVisible] = useState<boolean>(false);
  const [renderer, setRenderer] = useState<THREE.WebGLRenderer>();
  const [camera, setCamera] = useState<THREE.PerspectiveCamera>();
  const [scene, setScene] = useState<THREE.Scene>();
  const light1 = useMemo(() => new THREE.SpotLight(0xffffff, 0.8), []);
  const light2 = useMemo(() => new THREE.SpotLight(0xffffff, 0.4), []);
  const light3 = useMemo(() => new THREE.SpotLight(0xffffff, 0.4), []);
  const meshes = useState<THREE.Mesh[]>(generateMeshes())[0];

  // Intersection Observer to detect when component is in viewport
  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        setIsVisible(entry.isIntersecting);
      },
      { threshold: 0.1 }
    );

    if (canvasContainerRef.current) {
      observer.observe(canvasContainerRef.current);
    }

    return () => {
      if (canvasContainerRef.current) {
        observer.unobserve(canvasContainerRef.current);
      }
    };
  }, []);

  const getYOffset = useCallback(() => {
    if (!slideContainer) {
      return 0;
    }

    return slideContainer.getBoundingClientRect().top;
  }, [slideContainer]);

  const setYPos = useCallback(() => {
    if (!camera) {
      return;
    }
    camera.position.y = 1 - getYOffset() / -1000;
    light1.position.setY(-0.1 - getYOffset() / -180);
    light2.position.setY(1.2 - getYOffset() / -120);
    light3.position.setY(0.8 - getYOffset() / -140);
  }, [camera, getYOffset, light1.position, light2.position, light3.position]);

  useEffect(() => {
    if (!isVisible || !canvasContainerRef?.current) {
      return;
    }

    const canvasContainer = canvasContainerRef.current;
    const _camera = new THREE.PerspectiveCamera(
      70,
      canvasContainer.offsetWidth / canvasContainer.offsetHeight,
      0.01,
      10
    );
    _camera.position.z = 0.9;
    setCamera(_camera);

    const _scene = new THREE.Scene();

    meshes.forEach((mesh) => {
      _scene.add(mesh);
      (mesh.userData as any).basePosition = mesh.position.clone();
    });

    light1.position.set(0.2, -0.1, 1.0);
    light1.castShadow = true;
    _scene.add(light1);

    light2.position.set(0.3, 1.2, 0.8);
    light2.castShadow = true;
    _scene.add(light2);

    light3.position.set(0.1, 0.8, 0.2);
    light3.castShadow = true;
    _scene.add(light3);

    setScene(_scene);

    // Remove any existing canvas from previous renderer so the new one can be appended
    while (canvasContainer.firstChild) {
      canvasContainer.removeChild(canvasContainer.firstChild);
    }

    // Dispose previous renderer if present
    if (rendererRef.current) {
      try {
        rendererRef.current.dispose();
      } catch (e) {
        // ignore disposal errors
      }
      rendererRef.current = undefined;
    }

    const _renderer = new THREE.WebGLRenderer({
      antialias: true,
      preserveDrawingBuffer: true,
      alpha: true,
    });
    _renderer.setSize(canvasContainer.offsetWidth, canvasContainer.offsetHeight);
    rendererRef.current = _renderer;
    setRenderer(_renderer);
    // initialize last scroll position to avoid a large first-delta
    if (typeof window !== 'undefined') {
      lastScrollRef.current = window.scrollY || window.pageYOffset || 0;
    }
  }, [canvasContainerRef, light1, light2, light3, meshes, isVisible]);

  // Cleanup renderer on unmount
  useEffect(() => {
    return () => {
      if (rendererRef.current) {
        try {
          rendererRef.current.dispose();
        } catch (e) {
          // ignore
        }
        rendererRef.current = undefined;
      }
      if (canvasContainerRef.current) {
        canvasContainerRef.current.innerHTML = '';
      }
    };
  }, []);

  // Note: animation will be driven by scroll events only (see scroll handler below)

  useEffect(() => {
    if (!renderer || !canvasContainerRef?.current || !scene || !camera) {
      return;
    }
    if (canvasContainerRef.current.hasChildNodes() === false) {
      canvasContainerRef.current.appendChild(renderer.domElement);
    }
    setYPos();
    renderer.render(scene, camera);
  }, [renderer, canvasContainerRef, camera, scene, setYPos]);

  useEffect(() => {
    const scroll = () => {
      if (!scene || !camera || !renderer || !slideContainer || !isVisible) {
        return;
      }
      if (!canvasContainerRef?.current || !isWithinViewport(canvasContainerRef.current)) {
        return;
      }

      const now = (window && (window.scrollY || window.pageYOffset)) || 0;
      const delta = now - (lastScrollRef.current || 0);
      if (delta !== 0) {
        rotationRef.current += delta * 0.02;

        const baseTime = rotationRef.current;
        meshes.forEach((mesh, i) => {
          const t = i / MAX_CUBES;
          const angle = baseTime * 0.8 + t * Math.PI * 4;
          // Increase radius spacing to prevent overlaps
          const radius = 0.08 + t * 0.2;
          const base = (mesh.userData as any).basePosition as THREE.Vector3 | undefined;
          const baseY = base ? base.y : mesh.position.y;

          mesh.position.x = Math.cos(angle) * radius;
          mesh.position.z = Math.sin(angle) * radius * 0.5;
          mesh.position.y = baseY + Math.sin(baseTime * 1.5 + i) * 0.02 + t * 0.5;
        });

        lastScrollRef.current = now;
      }

      setYPos();
      renderer.render(scene, camera);
    };

    const resize = () => {
      if (!scene || !camera || !renderer || !slideContainer || !isVisible) {
        return;
      }
      if (!canvasContainerRef?.current || !isWithinViewport(canvasContainerRef.current)) {
        return;
      }
      const canvasContainer = canvasContainerRef.current;

      renderer.setSize(0, 0);
      requestAnimationFrame(() => {
        const { offsetWidth, offsetHeight } = slideContainer;
        const width = offsetWidth / 2;
        const height = offsetHeight;
        canvasContainer.style.width = `${width}px`;
        canvasContainer.style.height = `${height}px`;
        camera.aspect = width / height;
        camera.updateProjectionMatrix();
        renderer.setSize(width, height);
        renderer.render(scene, camera);
      });
    };

    window.addEventListener('scroll', scroll);
    window.addEventListener('resize', resize);

    return () => {
      window.removeEventListener('scroll', scroll);
      window.removeEventListener('resize', resize);
    };
  }, [camera, renderer, scene, setYPos, slideContainer, isVisible]);

  return <div className={classNames.art} ref={canvasContainerRef}></div>;
}
